@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html{
    font-family: 'Poppins', sans-serif;
}

.container-content{
    @apply mb-5 md:mt-5 lg:w-[70%]
}

.title-h2{
    @apply relative pl-2 mb-3 before:absolute before:w-1 before:h-full before:bg-slate-50 before:top-0 before:left-0 before:rounded-sm bg-slate-700 rounded-sm py-2;
}

aside{
    @apply lg:w-[28%] lg:mt-5 lg:fixed lg:right-5
}

tr, td{
    @apply border border-slate-50 p-2 text-sm
}

.card:hover .name{
    @apply h-fit py-2 transition-all duration-200
}

.scrollbar-hide::-webkit-scrollbar{
    @apply hidden md:block
}

.scrollbar-custom::-webkit-scrollbar{
    width: 5px;
    height: 5px;
    border-radius: 100px;
}

.scrollbar-custom::-webkit-scrollbar-thumb{
    @apply bg-red-600;
    border-radius: 100px;
}    

.season {
    display: grid;
    grid-template-areas: 
    'a'
    'b'
    'c';
}

.season .poster{
    grid-area: a;
}

.season .title{
    grid-area: b;
}

.season .overview{
    grid-area: c;
}

@media screen and (width >= 768px) {
    .season{
        display: grid;
        grid-template-areas: 
        'b b'
        'a c';
        gap: 20px;
    }
}
